import React from 'react';
import styled from 'styled-components';
import Fade from 'react-reveal/Fade'


function Section(props) {
    
  return (

    <Container id={props.identity} img={props.bgimg} >
        <Fade bottom>
            <Headingstyle>
                <TitleStyle>{props.title}</TitleStyle>

                <p style={{fontWeight:500}}>{props.disc}</p>
            </Headingstyle>
        </Fade>

        <div>
            <Fade bottom>
                <ButtonDiv>
                    <ButtonStyleL>{props.lbtn}</ButtonStyleL>
                    {props.rbtn && <ButtonStyleR >{props.rbtn}</ButtonStyleR>}
                </ButtonDiv>

                <DownArrowDiv src="/images/down-arrow.svg"/>
            </Fade>
        </div> 

    </Container>
  )
}

export default Section

const Container = styled.div`
    height: 100vh ;
    width: 100vw ;
    background-color: black;
    background-image : ${props => `url("/images/${props.img}")`};
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    display : flex ;
    flex-direction: column;
    justify-content: space-between ;
`

const Headingstyle = styled.div`
    padding-top : 15vh;
`

const TitleStyle = styled.h1`
    margin: 10px;
    font-size: 38px;
    letter-spacing: 0.1px;
`

const ButtonStyle = styled.div`
    width : 260px ;
    height : 40px;
    display: flex;
    justify-content : center;
    opacity: 0.9;
    align-items: center;
    border-Style: hidden;
    border-Radius: 5px;
    color: white;
    margin: 8px;
    cursor: pointer;
`

const ButtonStyleL = styled(ButtonStyle)`
    background-color: rgb(23,26,32,0.85);
`

const ButtonStyleR = styled(ButtonStyle)`
    background-color: white;
    color : black;
`

const ButtonDiv = styled.div`
    display : flex;
    justify-Content : center;
    margin-Bottom: 3vh;
    flex-Wrap: wrap;
`

const DownArrowDiv = styled.img`
    height : 40px ;
    margin: 10px ;
    animation: arrow infinite 1.5s ;

`
   
