import React from 'react'
import styled from 'styled-components'
import CloseIcon from '@material-ui/icons/Close'
import { useState } from 'react'
import { Link } from 'react-scroll'

function Header() {
  const [MenuVisible , setMenuVisible] = useState(false);

  return (
      <div>
      <Container>
          <Logo>
            <a>
              <img src="/images/logo.svg"/>
            </a>
          </Logo>

          <RNavigation>
            <CustomMedia>
              <a>Shop</a>
              <a>Account</a>
            </CustomMedia>
            
            <a onClick={() => setMenuVisible(true)}>Menu</a>
          </RNavigation>

      </Container>

      <RightMenu visible={MenuVisible}>
              <CustomClose>
                <CloseIcon style={{cursor:"pointer"}} onClick={() => setMenuVisible(false)}/>
              </CustomClose>
              <li><Link onClick={() => setMenuVisible(false)} to="models" spy={true} smooth={true} duration={1200} >Model S</Link></li>
              <li><Link onClick={() => setMenuVisible(false)} to="modely" spy={true} smooth={true}  duration={1200} >Model Y</Link></li>
              <li><Link onClick={() => setMenuVisible(false)} to="model3" spy={true} smooth={true}  duration={1200} >Model 3</Link></li>
              <li><Link onClick={() => setMenuVisible(false)} to="modelx" spy={true} smooth={true}  duration={1200} >Model X</Link></li>
              <li><Link onClick={() => setMenuVisible(false)} to="panel" spy={true} smooth={true}  duration={1200} >Solar Panels</Link></li>
              <li><Link onClick={() => setMenuVisible(false)} to="roof" spy={true} smooth={true}  duration={1200} >Solar Roof</Link></li>
              <li><a >Existing Inventory</a></li>
              <li><a >Used Inventory</a></li>
              <li><a >Trade-In</a></li>
              <li><a >Test Drive</a></li>
              <li><a >Insurace</a></li>
          </RightMenu>

      <Menu>
            <Link to="models" spy={true} smooth={true}  duration={1200} >Model S</Link>
            <Link to="modely" spy={true} smooth={true}  duration={1200} >Model Y</Link>
            <Link to="model3" spy={true} smooth={true}  duration={1200} >Model 3</Link>
            <Link to="modelx" spy={true} smooth={true}  duration={1200} >Model X</Link>
            <Link to="panel" spy={true} smooth={true}  duration={1200} >Solar Panels</Link>
            <Link to="roof" spy={true} smooth={true}  duration={1200} >Solar Roof</Link>
      </Menu>


    </div>
  )
}

export default Header

const Container = styled.div`
    z-index: 1;
    width: 100%;
    height: 50px;
    padding: 15px 20px;
    position: fixed;
    display : flex;
    align-items: center;
    justify-content: space-between;
`

const Logo = styled.div`
`

const Menu = styled.div`
    top : 0;
    bottom: 0;
    padding: 0 50px;
    z-index: 200;
    height: 50px;
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 80%;
    margin-left: 10%;
    margin-right: 10%;
    cursor: pointer;

    a{
      padding: 0 15px;
      font-weight: bold;
      letter-spacing: 0.1px;
    }

    a:hover {
      background-color: rgba(255,255,255,0.2);
      border-radius: 8px;
      padding : 7px 15px ;
      transition: background-color 0.4s ease-in-out;
    }

    @media(max-width : 1124px) {
      display : none;
    }

`

const RNavigation = styled(Menu)`
    margin-left : 20%;
    z-index: 1000;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding: 0 30px;
`

const RightMenu = styled.div`
    position : fixed;
    width: 300px;
    top : 0;
    right : 0;
    bottom : 0;
    background-color: white;
    z-index: 100;
    list-style: none;
    padding: 25px 50px;
    display:flex;
    flex-direction: column;
    align-items: flex-start;
    border-radius: 20px 0px 0 20px;
    transform : ${props => props.visible ? 'translateX(0)' : 'translateX(100%)' };
    transition: transform 0.25s ease-in-out;
    li{
      padding: 15px 0;
      cursor: pointer;
    }

`

const CustomClose = styled.div`
    width: 100%;
    display : flex;
    justify-content: flex-end;
    padding: 25px 0;
`

const CustomMedia = styled.div`
    @media(max-width : 1124px) {
      display : none;
    }
`